import React from "react";
import ReactDOM from "react-dom";
import 'normalize.css';
import "./style.css";
import play from "./assets/play.svg";
import stop from './assets/stop.svg';

class Synth extends React.Component {
  constructor(props) {
    super(props);
    this.msg = new SpeechSynthesisUtterance();
  }

  state = {
    options: null,
    text: "",
    rate: -1,
    voices: null
  };

  addVoices = () => {
    let list = speechSynthesis.getVoices();
    let lang = [...list];
    this.setState({
      voices: lang
    });
    list = list.map(item => {
      return (
        <option key={item.name} value={item.name}>
          {item.name} {item.lang}
        </option>
      );
    });
    this.setState({
      options: list
    });
  };

  toggleVoice = e => {
    let resultVoice = this.state.voices.find(
      value => value.name === e.target.value
    );

    this.msg.voice = resultVoice;
    this.restart(false);
  };

  componentDidMount() {
    speechSynthesis.addEventListener("voiceschanged", this.addVoices);
  }

  addText = e => {
    this.setState({
      text: e.target.value
    });

    this.msg.text = this.state.text;
  };

  restart(stop) {
    if (stop) {
      return speechSynthesis.cancel();
    }
    speechSynthesis.cancel();
    speechSynthesis.speak(this.msg);
  }

  setSpeed = e => {
    let nameOfPropery = e.target.name;
    this.setState({
      [nameOfPropery]: e.target.value
    });

    this.msg.rate = this.state.rate;
    this.restart(false);
  };

  render() {
    this.msg.text = this.state.text;
    return (
      <div className="container">
        <h1 className="title">Синтезатор текста</h1>
        <div className="select">
          <select onChange={this.toggleVoice} name="slct" id="slct" className="voices">
            <option value="choose">выберите язык</option>
            {this.state.options}
          </select>
        </div>
        <div className="slidecontainer">
          <h2 className="speed">Cкорость</h2>
          <input
            type="range"
            className="slider"
            onChange={this.setSpeed}
            value={this.state.rate}
            name="rate"
            min="-1"
            max="3"
            step="0.5"
          />
        </div>
        <div className="text-field">
          <textarea
            onChange={this.addText}
            name="text"
            id="text-data"
            placeholder="введите текст здесь"
          />
        </div>
        <div className="button-wrapper">
          <button
            key={1}
            onClick={() => this.restart(true)}
            className="button-control"
          >
            
            <img alt="остановить" width="40" height="40" src={stop} />
          </button>
          <button
            key={2}
            onClick={() => this.restart(false)}
            className="button-control"
          >
            <img alt="включить" width="40" height="40" src={play} />
          </button>
        </div>
      </div>
    );
  }
}

const rootElement = document.getElementById("root");
ReactDOM.render(<Synth />, rootElement);
